import * as THREE from "three"
import  { OrbitControls, MapControls } from "three/examples/jsm/controls/OrbitControls";
class Group {

    scene: THREE.Scene
    camera: THREE.OrthographicCamera
    renderer: THREE.WebGLRenderer
    mesh: THREE.Mesh

    controls: any
    clock: THREE.Clock = new THREE.Clock()
    T0: Date
    constructor() {
         /**
     * Scene
     */
    var scene = new THREE.Scene();
    /**
     * 
     */
    var geometry = new THREE.BoxGeometry(20, 20, 20); //Geometry
    var material = new THREE.MeshLambertMaterial({
      color: 0x0000ff
    }); //Material

    // meshx
     var group1 = new THREE.Group();
    // mesh
    for (let i = 0; i < 10; i++) {
      var mesh = new THREE.Mesh(geometry, material); // 
      mesh.translateX(i * 25); // 
      scene.add(mesh);//
      group1.add(mesh); //group1
    }

    //group1y
    var group2 = new THREE.Group();
    for (let i = 0; i < 10; i++) {
      var newGroup = group1.clone(); // group1
      newGroup.translateY(i * 25); //z
      scene.add(newGroup); //group1
      group2.add(newGroup); //group2group1
    }

    // group2z
    for (let i = 0; i < 10; i++) {
      var newGroup = group2.clone(); // group2
      newGroup.translateZ(i * 25); //z
      scene.add(newGroup); //group2
    }

    // 
    var axesHelper = new THREE.AxesHelper(200);
    scene.add(axesHelper);
    /**
     * 
     */
    //
    var point = new THREE.PointLight(0xffffff);
    point.position.set(400, 200, 300); //
    scene.add(point); //
    //
    var ambient = new THREE.AmbientLight(0x444444);
    scene.add(ambient);
    /**
     * 
     */
    var width = window.innerWidth; //
    var height = window.innerHeight; //
    var k = width / height; //
    var s = 150; //
    //
    var camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000);
    camera.position.set(200, 300, 200); //
    camera.lookAt(scene.position); //()
    /**
     * 
     */
    var renderer = new THREE.WebGLRenderer();
    renderer.setSize(width, height); //
    renderer.setClearColor(0xb9d3ff, 1); //
    document.body.appendChild(renderer.domElement); //bodycanvas

    // 
    function render() {
      renderer.render(scene, camera); //
    }
    render();

    var controls = new OrbitControls(camera,renderer.domElement);//
    controls.addEventListener('change', render);//
  
    }
}

export {Group}
